<script>
	export default {
		data() {
			return {
				totalProvince:{},
				cityNameNumber:{},
				nowProvince:"湖北",
				nowCity:"武汉",
				nowCityNumber:"",
				cityData:"",
				weatherList:[],
				tableContent:""
			};
		},
		mounted(){
			$(function () {
			  $('[data-toggle="popover"]').popover();
			})
			$('.navbar-nav> li').click(function(){
				$(this).addClass('active').siblings().removeClass('active');
			});
			this.queryCity();
			// this.queryCityWeather();
			
		},
		methods: {
			async queryCity(){
				 var that=this;
				 await this.$get('/rest/bzyycloud/weather/cityquery?province=&cityName=')
				  .then(response => {
					this.readProvinceCity(that,response);
					this.queryCityWeather(that);
				  }).catch(err => {
					  console.log(err);
				  })
			},
			
			queryCityWeather(that){
				console.log(that.cityNameNumber);
				// var targetCity=this.nowCity;
				this.nowCityNumber=that.cityNameNumber.get(this.nowCity);
				this.$get('/rest/bzyycloud/weather/cityweather?cityNumber='+this.nowCityNumber)
				 .then(response => {
					  console.log(response);
					  var yesterday=response.data.yesterday;
					  var forecast=response.data.forecast;
					  var cityName=response.data.city;
					 
					  var data="";
					 
					  var trs='<tr>'+
							  '<td>'+yesterday.date+'</td>'+
							  // '<td>'+yesterday.fengli+'</td>'+
							  '<td>'+yesterday.fengxiang+'</td>'+
							  '<td>'+yesterday.high+'</td>'+
							  '<td>'+yesterday.low+'</td>'+
							  '<td>'+yesterday.type+'</td>'+
							  '</tr>';
					  $.each(forecast, function (i, iteam) {
						  if(i==0){
							  data=iteam.date;
						  }
						  trs+='<tr>'+
							  '<td>'+iteam.date+'</td>'+
							  // '<td>'+iteam.fengli+'</td>'+
							  '<td>'+iteam.fengxiang+'</td>'+
							  '<td>'+iteam.high+'</td>'+
							  '<td>'+iteam.low+'</td>'+
							  '<td>'+iteam.type+'</td>'+
							  '</tr>';
					  }) ;
					  this.cityData=cityName+" "+data;
					  var table='<div class="table-responsive"><table class="table table-striped table-condensed"><tbody id="body" >'+
					  					trs+			
							+'</tbody></table></div>';
					this.tableContent=table;
					// var a='<a tabindex="0" class="btn btn-default" role="button" data-toggle="popover" data-trigger="focus" title="天气预报" data-content="\""'+table+'"\"">{{cityData}}</a>';
					// $("#timeWeather").html(a); 
				    $('[data-toggle="popover"]').popover({
						html:true
					});
				 }).catch(err => {
					  console.log(err);
				 })
			},
			
			readProvinceCity(that,response){
				var json=response;
				var map = new Map();
				var ProvinceMap=new Map();
				for(var province in json){
					var pName=province;
					var pCity=[];
					var singleProvince=json[province];
					for(var city in singleProvince){
						pCity.push(city);
						var cityName=city;
						var cityNum=singleProvince[cityName];
						// that.cityNameNumber[cityName]=cityNum;
						map.set(cityName,cityNum);
					}
					ProvinceMap.set(pName,pCity);
				}
				that.cityNameNumber=map;
				that.totalProvince=ProvinceMap;
				console.log(that.cityNameNumber);
				console.log(that.totalProvince);
			},
		}
	}
</script>

