import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import prefData from "@/pages/prefData.vue";
import firstcontent from "@/pages/firstcontent.vue";


Vue.use(Router)

export default new Router({
	mode: 'hash',
  routes: [
    {
      path: '*',
      name: 'Home',
      component: Home,
			children:[
				{path:"prefData",name:"prefData",component:prefData},
				{path:"firstcontent",name:"firstcontent",component:firstcontent}
				]
    }
  ]
})



