<template>
	<div>
		 <canvas id="chart" style="width: 80;height: 80;"></canvas>
		 
		 <div style="margin-top: 10px;">
			 <div class="col-xs-3 col-sm-2">
				 展示最近天数：
			 </div>
			  <div class="col-xs-3 col-sm-2">
			 	<select class="form-control" id="pagetStart" @change="GetPageStart()">
			 		<option>7</option>
			 		<option>15</option>
			 		<option>30</option>
			 	</select>
			 </div>
		 </div>
	</div>
</template>

<script>
	import Chart from 'chart.js';
	export default{
		data(){
			return{
				cpuUsage:[],
				diskUsed:[],
				memUsed:[],
				updateTime:[],
				pageStart:0
			}
		},
		methods:{
			queryPerdata(){
				var that;
				this.$get('/rest/bzyycloud/prefdata/querydata?pageStart='+this.pageStart)
				 .then(response => {
					  console.log(response);
					  that=this;
					  var array=response.data;
					  var total=response.total;
					  $.each(array,function(index,iteam){
						  var cpu=iteam.cpuUsage;
						  var disk=iteam.diskUsed;
						  var memery=iteam.memUsed;
						  var time=iteam.updateTime;
						  
						  that.cpuUsage.push(cpu);
						  that.diskUsed.push(disk);
						  that.memUsed.push(memery);
						  that.updateTime.push(time);
					  });
					  console.log(that.cpuUsage);
					  this.showChart();
				 }).catch(err => {
					  console.log(err);
				 })
			},
			showChart(){
				var ctx=document.getElementById("chart");
				new Chart(ctx,{				  
					  type: 'line',
					  data: {
						labels: this.updateTime,
						datasets: [
						  {
							label:"cpu使用率",
							data: this.cpuUsage,
							backgroundColor: "#fff", //背景填充色
							borderColor: "#36A2EB", //路径颜色
							borderWidth: 2
						  },
						  {
							label: '磁盘空闲率',
							data:this.diskUsed,
							backgroundColor:  "#fff", //背景填充色
							borderColor:  "#008800", //路径颜色
							borderWidth: 2
						  },{
							label: '内存使用率',
							data:this.memUsed,
							backgroundColor:  "#fff", //背景填充色
							borderColor: "#583E7E", //路径颜色
							borderWidth: 2
						  }
						]
					  },
					  options: {
						  title: {
							display: true,
							text: '性能监控' //标题
						   }
					  }
				});
			},
			
			GetPageStart(){
				var page=$("#pagetStart").val();
				this.pageStart=page;
				this.queryPerdata();
			}
		},
		mounted(){
			this.showChart();
			this.queryPerdata();
		}
	}
</script>

<style>
</style>



