<template>
	<div>
		<div class="row">
			<div class="box col-xs-12 col-sm-6 col-md-6 col-lg-5">
			    <ul class="image">
			        <li><img src="../../static/photo/oldhouse.jpg"/></li>
			        <li><img src="../../static/photo/qsmy.jpg"/></li>
			        <li><img src="../../static/photo/lh.jpg"/></li>
			        <li><img src="../../static/photo/bird.jpg"/></li>
			        <li><img src="../../static/photo/flower.jpg"/></li>
			        <li><img src="../../static/photo/college.jpg"/></li>
			    </ul>
				<div class="col-xs-12">
					<ul class="num">
					    <li class='current'>1</li>
					    <li>2</li>
					    <li>3</li>
					    <li>4</li>
					    <li>5</li>
					    <li>6</li>
					</ul>
					<div class="left arrow"><</div>
					<div class="right arrow">></div>
				</div>
			</div>
		</div>
		<!-- <div class="row">
			<div class="col-xs-4">
				<ul class="num">
				    <li class='current'>1</li>
				    <li>2</li>
				    <li>3</li>
				    <li>4</li>
				    <li>5</li>
				    <li>6</li>
				</ul>
				<div class="left arrow"><</div>
				<div class="right arrow">></div>
			</div>
		</div> -->
	</div>
</template>

<script>
	export default {
		data() {
			return {
			}
		},
		mounted() {
			var i=0,timer;
			var sum = $(".image li").length;
			$(function(){
				$(".image li").eq(0).show();
				
				$(".box").hover(function(){
					$(".arrow").show();
					clearInterval(timer);
				},function(){
					$(".arrow").hide();
					iLunbo();
				})
				iLunbo();
				
				$(".arrow").hover(function(){
					clearInterval(timer);
				});
				/*左箭头控制轮播*/
				$(".left").click(function(){
					clearInterval(timer);
					if(i==0){
						i=sum;
					}
					i--;
					startLunbo();
					iLunbo();
				});
				
				/*右箭头控制轮播*/
				$(".right").click(function(){
					clearInterval(timer);
					if(i==sum-1){
						i=-1;
					}
					i++;
					startLunbo();
					iLunbo();
				});
				
				/*提示信息变换*/
				$(".num>li").hover(function(){
					clearInterval(timer);
					i=$(this).index();
					console.log(i);
					startLunbo();
				});
			});
			
			/*自动轮播*/
			function iLunbo(){
				timer = setInterval(function(){
					i++;
					if(i==sum-1){
						i=-1;
					}
					startLunbo();
				},3000)
			}
			
			/*图片轮播和提示信息*/
			function startLunbo(){
				if(i==6){
					i=0;
				}
				$(".image>li").eq(i).fadeIn().siblings().fadeOut();
				$(".num>li").eq(i).addClass("current").siblings().removeClass("current");
			}
		}
	}
	
</script>

<style scoped>
	*{
		margin: 0;
		padding: 0;
	}
	.image{
		list-style: none;
	}
	.box{
		/* width: 100%; */
		height:30%;
		position: relative;
		/* margin: 20px auto; */
	}
	.image li img{
		width: 100%;
		height: 200px;
		width: ;
		border-radius: 15px;
	}
	.image li{
		width: 500px;
		height: 300px;
		float: left;
		position: absolute;
		display: none;
	}
	.num{
		position: relative;
		list-style: none;
		cursor: pointer;
		bottom: 20px;
		left: 80px;
		top: 180px;
		width: 100%;
	}
	.num li{
		float: left;
		color: white;
		width: 20px;
		height: 20px;
		border-radius: 100%;
		background-color: gray;
		margin: 0 4px;
		text-align: center;
		line-height: 20px;
	}
	.num .current{
		background-color: red;
	}
	.arrow{
		height: 60px;
		width: 30px;
		position: absolute;
		cursor: pointer;
		background-color: black;
		color: white;
		opacity: 0.5;
		font-size: 20px;
		text-align: center;
		line-height: 60px;
		top: 100px;
		display: none;
	}
	.left{
		left: 0;
	}
	.right{
		right: 0;
		left: 460px;
	}
	
	
</style>



